<template>
    <div class="m-detail m-customer-detail" v-loading="state.loading">
        <div class="m-info">
            <div class="m-row m-row-user">
                <img class="u-info-avatar" :src="state.data.avatar" :alt="state.data.name" />{{
                    state.data.name || "-"
                }}
            </div>
            <div class="m-row">
                <span class="u-label">userId</span>
                <span class="u-value">{{ state.data.userId || "-" }}</span>
            </div>
            <div class="m-row">
                <span class="u-label">手机</span>
                <span class="u-value">{{ state.data.mobile || "-" }}</span>
            </div>
            <div class="m-row">
                <span class="u-label">邮箱</span>
                <span class="u-value">{{ state.data.email || "-" }}</span>
            </div>
            <div class="m-row">
                <span class="u-label">创建时间</span>
                <span class="u-value">{{ state.data.updatedAt || "-" }}</span>
            </div>
            <div class="m-row">
                <span class="u-label">状态</span>
                <span class="u-value">{{ state.data.disabled == "0" ? "正常" : "停用" }}</span>
            </div>
        </div>
    </div>
</template>
<script setup>
import { reactive, onMounted, ref } from "vue";
import { getUserInfo } from "@/service/manage";
import { useRoute } from "vue-router";
import { storeToRefs } from "pinia";
import { deepBerryStore } from "@/store/index";
import { getUserLogin, getUserPermission } from "@/service/index.js";
const store = deepBerryStore();
let { role } = storeToRefs(store);
const route = useRoute();
route;
// 数据
let state = reactive({
    data: {},
    loading: false,
});
const first = ref(true);
const code = ref(document.location.search.split("&")[0]);
if (code.value && first) {
    first.value = false;
    getUserLogin(code.value)
        .then((res) => {
            console.log("getUserLogin获取的res:", res);
            localStorage.setItem("token", JSON.stringify(res.data.accessToken));
            getUserInfo().then((res) => {
                getUserPermission(res.data.userId).then((res) => {
                    role = res.data.viewPermissoins;
                    store.role = role;
                });
            });
        })
        .catch((err) => {
            console.log(err);
        });
}

onMounted(() => {
    if (localStorage.getItem("token")) {
        console.log(localStorage.getItem("token"));
        state.loading = true;
        getUserInfo()
            .then((res) => {
                state.data = res.data.data;
            })
            .finally(() => (state.loading = false));
    }
});
</script>
